.moviesSearchForm {
  margin-top: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.moviesSearchInput {
  border-radius: 10px;
  border: 1px solid rgba(0, 0, 0, 0.261);
  outline: none;
  padding: 10px 15px;
  font-size: 18px;
  color: black;
}

.moviesSearchBtn {
  background-color: black;
  color: white;
  padding: 10px 15px;
  font-size: 18px;
  border-radius: 10px;
  border: 1px solid transparent;
  transition: all 250ms ease-in-out;
}

.moviesSearchBtn:is(:focus, :hover) {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  transform: scale(1.03);
  cursor: pointer;
}
